import React from 'react';
import Link from 'next/link';

import { printDate } from '../services/utils';

export default class RecentArticles extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {

        const blogs = this.props.articles.map(c => {
            return <React.Fragment key={c.blogId}>
                <div className="col-sm-12 col-xl-4 col-md-4 col-lg-4 recent-card">
                    <div className="title">{c.title}</div>
                    <div className="details">{printDate(c.date) + ' - ' + c.readTimeMin + ' mins read'}</div>
                    <Link href={`/blog/${c.blogId}.html`}>read more</Link>
                </div>
            </React.Fragment>;
        });

        return <React.Fragment>
            {blogs}
        </React.Fragment>;
    }

}
